	import { debounce } from '@/common/utils.js';
export default {
    data() {
        return {
            isPassword: true,
            isPassword1: true,
			n:true,
            password: '',
            newPassword: '',
            newPassword1: ''
        };
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
	computed: {
		allowLogin () {
			return !!(this.password && this.newPassword && this.newPassword1)
		},
	},
    methods: {
        show: function() {
            this.isPassword = !this.isPassword;
        },
        show1: function() {
            this.isPassword1 = !this.isPassword1;
        },
        getPassword: function(e) {
            this.password = e.detail.value;
        },
        getNewPassword: function(e) {
            this.newPassword = e.detail.value;
        },
        getNewPassword1: function(e) {
            this.newPassword1 = e.detail.value;
        },
		checkPwd:function(e){
			this.newPassword = e.detail.value;
			var f = this.global_.checkPassword(this.newPassword);
			if (!f) {
			    uni.showToast({
			        title: '交易密码为6位数字',
			        icon: 'none',
			        duration: 2000
			    });
			    return false;
			}
		},
		next: function() {
			var that=this;
			that.n = false;
		},
		back: function() {
			var that=this;
			that.n = true;
		},
        changePassword: function() {
            if (this.password == '') {
                uni.showToast({
                    icon: 'none',
                    title: '请输入现有密码',
                    duration: 2000
                });
                return false;
            }
            if (this.newPassword == '') {
                uni.showToast({
                    icon: 'none',
                    title: '请输入新的交易密码',
                    duration: 2000
                });
                return false;
            }
			if (this.newPassword && this.newPassword1 && this.newPassword1 !== this.newPassword) {
			    uni.showToast({
			        icon: 'none',
			        title: '两次密码不一致',
			        duration: 2000
			    });
			    return false;
			}
            var f = this.global_.checkPassword(this.newPassword);
            if (!f) {
                uni.showToast({
                    title: '交易密码为6位数字',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.newPassword1 == '') {
                uni.showToast({
                    icon: 'none',
                    title: '请确认新的交易密码',
                    duration: 2000
                });
                return false;
            }
           
            uni.request({
                url: this.url + 'updatapasswods/',
                method: 'POST',
                data: {
                    password1: this.password,
                    password2: this.newPassword,
                    password3: this.newPassword1
                },
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 200) {
                       
                        uni.navigateBack({
							dekta:2
						})
						uni.showToast({
						    title: '资金密码修改成功',
						    icon: 'none',
						    duration: 1500
						});
                    }
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '现有密码错误，或是新旧密码一样',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                }
            });
        },
		linkToTransfer: debounce(function(){
			uni.navigateTo({
			    url: '../change-otherPassword/change_otherPassword'
			});
		},500, true),
        other: function() {
			this.linkToTransfer()
        }
    }
};
